.textshuffle-wrapper {
  padding: 0;
  background-color: white;
  height: 150px;
  position: relative;
  width: 100%;
  cursor: default;
}

.textshuffle {
  color: rgba(0, 0, 255, 0.75);
  width: 316px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 5px auto 45px auto;
}
.textshuffle-shape {
  position: relative;
}
.textshuffle-shape > * {
  width: 50%;
  height: 64px;
  position: absolute;
  overflow: hidden;
}
.textshuffle-shape > * > * {
  width: 100%;
  height: 100%;
  background: darkorange;
}
.textshuffle-shape .shape-left {
  left: 0;
}
.textshuffle-shape .shape-right {
  right: 0;
}
.textshuffle > .title {
  font-size: 58px;
  letter-spacing: 8px;
  position: absolute;
  min-width: 50px;
  overflow: hidden;
  text-align: center;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.textshuffle-bar {
  width: 100%;
  height: 1px;
  background: orangered;
  position: absolute;
  top: 70px;
}
.textshuffle .content {
  font-size: 12px;
  position: absolute;
  top: 78px;
  width: 100%;
  text-align: center;
}

.chapterflip-wrapper {
  position: relative;
  overflow: hidden;
  height: 800px;
  transition: background 1s;
  margin-top: -10px;
}

.chapterflip {
  margin: 150px 25%;
  width: 50%;
  height: 500px;
  background: #fff;
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}

.chapterflip-img-wrapper,
.chapterflip-text-wrapper {
  width: 50%;
  display: inline-block;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.chapterflip-text-wrapper .banner-anim-elem {
  width: 80% !important;
}

.chapterflip-pic,
.chapterflip-map {
  position: absolute;
}

.chapterflip-pic {
  width: 75%;
  margin: auto 12.5%;
}

.chapterflip-map {
  width: 250px;
  top: 60px;
  left: 30px;
}
.chapterflip-map.map2{
  width: 200px;
  left: 50px;
}

.chapterflip-text-wrapper .banner-anim-elem {
  padding: 28px;
  margin-top: 18px;
  margin-left: -6px;
}

.chapterflip-text-wrapper .banner-anim-elem:nth-child(7),
.chapterflip-text-wrapper .banner-anim-elem:nth-child(8),
.chapterflip-text-wrapper .banner-anim-elem:nth-child(9) {
  margin-top: -16px;
}

.chapterflip-text-wrapper h1 {
  font-size: 14px;
  margin: 5px auto;
}

.chapterflip-text-wrapper em {
  height: 2px;
  width: 60px;
  border-radius: 2px;
  display: block;
}

.chapterflip-text-wrapper p {
  font-size: 10px;
  margin-top: 14px;
}

.chapterflip-wrapper .anticon{
  position: absolute;
  font-size: 24px;
  top: 50%;
  margin-top: -12px;
  cursor: pointer;
  color: rgba(0, 0, 255, 0.75);
}

:focus {
  outline: -webkit-focus-ring-color auto 0;
  outline-width: 0;
}


.chapterflip-wrapper .anticon-caret-left{
  left: 5px;
  z-index: 999;
}

.chapterflip-wrapper .anticon-caret-right{
  right: 5px;
}


@media screen and (max-width: 1024px) {
  .chapterflip-text-wrapper p {
  font-size: 7.5px;
  }
  .chapterflip-text-wrapper h1 {
    font-size: 12px;
  }
}

@media screen and (max-width: 750px) {
  .chapterflip {
    width: 90%;
    margin: 150px 5%;
    transition: all 0.9s;
  }
}

@media screen and (max-width: 450px) {
  .chapterflip-text-wrapper .banner-anim-elem {
    margin-left: -16px;
  }
  .chapterflip-text-wrapper h1 {
    margin: 0 auto;
  }
}

@media screen and (max-width: 390px) {
  .chapterflip {
    width: 100%;
    margin: 150px 0;
    transition: all 0.9s;
  }
  .chapterflip-text-wrapper h1 {
    font-size: 9px;
  }
}
